.digiAccounts {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  color: white;
  text-align: center;
  height: 55px;
}

.accs {
  cursor: pointer;
  font-size: 18px;
  width: 35px;
  margin: 1px 5px 1px 5px;
  transition: all 0.3s ease;
}
.accs:hover {
  font-size: 28px;
}

.hr {
  margin: 20px 25px 0px 25px;
  border-width: 1px;
  border-color: rgb(236, 236, 236);
  border-style: solid;
}

.copy {
  height: fit-content;
  color: white;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 15px 0px 0px 10px;
  font-size: 14px;
}

.back-btn {
  border: 1.3px solid lightgray;
  background: none;
  color: white;
  border-radius: 4px;
  font-weight: 600;
  margin-left: 5px;
  padding: 3px 14px 3px 14px;
  transition: all 0.3s ease-in-out;
}

.back-btn:hover {
  background: white;
  color: #14131a;
}
@media (max-width: 900px) {
  .accs {
    font-size: 24px;
    margin: 1px 8px 1px 8px;
  }
  .digiAccounts {
    justify-content: center;
  }
  .copy {
    justify-content: center;
    margin: 15px 0px 0px 0px;
  }
}

.copy b {
  margin-left: 5px;
}
